@font-face {
    font-family: 'Formula font';
    src: url(./font/Formula1-Regular.otf);
}

body {
    font-family: 'Formula font',sans-serif;
    background-color: #202221;
}

header {
    background-image: url('./images/image.jpg');
    background-size: cover;
    background-position: center;
    height: 95vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
  }

  a:hover {
    transform: scale(1.2);
  }
  .custom-heading {
    font-size: 48px;
    font-weight: bold;
    margin-top: 1.5rem; /* equivalent to mt-4 */
    transition: color 0.3s, transform 0.3s;
  }
  
  .custom-heading:hover {
    color: #d1d5db; /* Tailwind's gray-300 equivalent */
    transform: scale(1.1); /* Example of scaling on hover */
  }
  
  
  footer {
    background-color: #181818;
    color: #ffffff;
  }
  